//
// KU mobile menu
// --------------------------------------------------

.local-menu {
  font-size: @font-size-large;
  color: @white-color;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  padding: @padding10 @padding10 @padding10 @padding20;
  background: @ku-red;
  border-bottom: 1px solid lighten(@ku-red, 7%);

  .theme-samf & {
    background: @samf;
    border-bottom: 1px solid lighten(@samf, 7%);
  }

  .theme-hum & {
    background: @hum;
    border-bottom: 1px solid lighten(@hum, 7%);
  }

  .theme-science & {
    background: @science;
    border-bottom: 1px solid lighten(@science, 7%);
  }

  .theme-sund & {
    background: @sund;
    border-bottom: 1px solid lighten(@sund, 7%);
  }

  .theme-jura & {
    background: @jura;
    border-bottom: 1px solid lighten(@jura, 7%);
  }

  .theme-teol & {
    background: @teol;
    border-bottom: 1px solid lighten(@teol, 7%);
  }
}
@media (max-width: @screen-sm-max) {
  // Hide KU language switcher icons
  &.dk,
  &.eng {
    > span,
    img {
      display: none;
    }
  }
}
// Mmenu mobile settings

.mm-menu {
  margin: 0;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 0;
  display: block;
  padding: 0;
  position: absolute;
  background-color: @white-color;
  //border-color: rgba(0,0,0,.1);
  color: @gray-base;

  #navbar_menu,
  #topbar_menu {
    li:not(.mm-opened) {
      border-bottom: 1px solid lighten(@gray, 10%);

      &:hover {
        background: lighten(@gray-base, 90%); //#e6e6e6
        transition: all 0.2s ease-in-out;
      }
    }

    li {
      &.mm-opened {
        background: lighten(@gray-base, 80%);
      }
    }
  }

  #leftmenu_2,
  #localmenu {
    // Faculty colors for sublevel 1
    background: @ku-red;

    .theme-samf & {
      background: @samf;
    }

    .theme-hum & {
      background: @hum;
    }

    .theme-science & {
      background: @science;
    }

    .theme-sund & {
      background: @sund;
    }

    .theme-jura & {
      background: @jura;
    }

    .theme-teol & {
      background: @teol;
    }
    color: @white-color;

    .mm-next {
      &:after {
        // Arrows
        border-color: @white-color;
      }
    }
// 1px solid
    li {
      border-bottom: lighten(@ku-red, 7%);

      .theme-samf & {
        border-bottom: lighten(@samf, 7%);
      }

      .theme-hum & {
        border-bottom: lighten(@hum, 7%);
      }

      .theme-science & {
        border-bottom: lighten(@science, 7%);
      }

      .theme-sund & {
        border-bottom: lighten(@sund, 7%);
      }

      .theme-jura & {
        border-bottom: lighten(@jura, 7%);
      }

      .theme-teol & {
        border-bottom: lighten(@teol, 7%);
      }
    }
  }

  .mm-navbar {
    a {
      color: @gray-light;
    }

    > * {
      color: @gray-light;
    }
  }

  .mm-btn {
    &:after {
      border-color: @gray-light;
    }

    &:before {
      border-color: @gray-light;
    }
  }

  .mm-listview {
    border-color: transparent;

    > li {
      .mm-arrow {
        &:after {
          border-color: @gray-light;
        }
      }

      .mm-next {
        &:after {
          // Arrows 1. level
          border-color: @gray-dark;
        }
      }

      a {
        &:not(.mm-next) {
          -webkit-tap-highlight-color: rgba(255,255,255,.5);
          tap-highlight-color: rgba(255,255,255,.5);
        }
      }
    }

    > li.mm-selected {
      > a {
        &:not(.mm-next) {
          // Selected item
          text-decoration: underline;
          font-weight: bold;
        }
      }

      > span {
        .backgroundColorFix(@white-color, .5);
      }
    }

    > li.mm-opened.mm-vertical {
      > a.mm-next {
        .backgroundColorFix(@gray-base, .08);
      }
    }
  }

  .mm-divider {
    .backgroundColorFix(@white-color, .05);
  }

  &:after {
    content: "";
    display: block;
    width: 1px;
    height: 120%;
    position: absolute;
    left: 100%;
    top: -10%;
    z-index: 99;
    box-shadow: 0 0 10px rgba(0,0,0,.3);
  }
}

.mm-panels {
  margin: 0;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 0;
  background: inherit;
  border-color: inherit;
  position: absolute;
  overflow: hidden;

  > .mm-panel {
    margin: 0;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 0;
    background: inherit;
    border-color: inherit;
    position: absolute;
    overflow: scroll;
    overflow-x: hidden;
    overflow-y: auto;
    padding: 0 20px;

    &:not(.mm-hidden) {
      display: block;
    }

    &:after {
      content: '';
      display: block;
      height: 20px;
    }

    &:before {
      content: '';
      display: block;
      height: 20px;
    }

    > .mm-listview {
      margin: 20px -20px;

      &:first-child {
        margin-top: -20px;
      }
    }

    > .mm-navbar+.mm-listview {
      margin-top: -20px;
    }
  }

  > .mm-panel.mm-hasnavbar {
    padding-top: 40px;
  }
}

.mm-listview {
  a {
    text-decoration: none;

    &:hover {
      text-decoration: none;
    }

    &.active {
      text-decoration: underline;
    }
  }

  .mm-divider {
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    font-size: 10px;
    text-transform: uppercase;
    text-indent: 20px;
    line-height: 25px;
  }

  > li {
    > a {
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
      color: inherit;
      display: block;
      padding: 10px 10px 10px 20px;
      margin: 0;
      line-height: 20px;
    }

    > span {
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
      color: inherit;
      display: block;
      padding: 10px 10px 10px 20px;
      margin: 0;
      line-height: 20px;
    }
    list-style: none;
    display: block;
    //padding: 0;
    margin: 0;
    position: relative;
    //border-color: inherit; //Border before arrow
    .mm-next {
      border-color: inherit;
      //   &:before {
      //     border-color: inherit;
      //   }
    }
    // &:after {
    //   border-color: inherit;
    // }
    > a.mm-arrow {
      padding-right: 50px;
    }

    > span.mm-arrow {
      padding-right: 50px;
    }
    // &:not(.mm-divider) {
    //   &:after {
    //     content: '';
    //     border-bottom-width: 1px;
    //     border-bottom-style: solid;
    //     display: block;
    //     position: absolute;
    //     right: 0;
    //     bottom: 0;
    //     left: 20px;
    //   }
    // }
  }

  .mm-vertical {
    .mm-panel {
      display: none;
      //padding: 10px 0 10px 10px;
      .mm-listview {
        > li {
          &:last-child {
            &:after {
              border-color: transparent;
            }
          }
        }
      }
    }
  }

  > li.mm-vertical {
    > .mm-next {
      height: 40px;
      bottom: auto;

      &:after {
        top: 16px;
        bottom: auto;
      }
    }
  }

  > li.mm-vertical.mm-opened {
    > .mm-next {
      &:after {
        transform: rotate(45deg);
        right: 19px;
      }
    }
  }
  list-style: none;
  display: block;
  padding: 0;
  margin: 0;
  font: inherit;
  font-size: @font-size-base;
  @media screen and (min-width: @screen-sm) and (max-width: @screen-sm-max) {
      font-size: @font-size-large;
  }
  line-height: 20px;

  .mm-next {
    //background: rgba(3,2,1,0);
    width: 50px;
    padding: 0;
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 2;
    // &:before {
    //   content: '';
    //   border-left-width: 1px;
    //   border-left-style: solid;
    //   display: block;
    //   position: absolute;
    //   top: 0;
    //   bottom: 0;
    //   left: 0;
    // }
  }

  .mm-next+a {
    margin-right: 50px;
  }

  .mm-next+span {
    margin-right: 50px;
  }

  .mm-next.mm-fullsubopen {
    width: 100%;

    &:before {
      border-left: none;
    }
  }

  .mm-next.mm-fullsubopen+a {
    padding-right: 50px;
    margin-right: 0;
  }

  .mm-next.mm-fullsubopen+span {
    padding-right: 50px;
    margin-right: 0;
  }

  .mm-inset {
    list-style: disc inside;
    padding: 0 10px 15px 40px;
    margin: 0;

    > li {
      padding: 5px 0;
    }
  }

  .mm-spacer {
    padding-top: 40px;

    > .mm-next {
      top: 40px;
    }
  }

  .mm-spacer.mm-divider {
    padding-top: 25px;
  }
}

.mm-navbar {
  a {
    text-decoration: none;

    &:hover {
      text-decoration: none;
    }
  }

  .mm-title {
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
  }
  border-bottom: 1px solid;
  border-color: inherit;
  text-align: center;
  line-height: 20px;
  height: 40px;
  padding: 0 40px;
  margin: 0;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;

  > * {
    display: block;
    padding: 10px 0;
  }

  .mm-btn {
    &:first-child {
      padding-left: 20px;
      left: 0;
    }

    &:last-child {
      text-align: right;
      padding-right: 20px;
      right: 0;
    }
  }
}

.mm-hidden {
  display: none !important;
}

.mm-wrapper {
  overflow-x: hidden;
  position: relative;
}

.mm-btn {
  z-index: 1;
  width: 40px;
  height: 40px;
  position: absolute;
  top: 0;
}

.mm-panel.mm-highest {
  z-index: 1;
}

.mm-panel {
  transition: transform 0.4s ease;
  transform: translate3d(100%,0,0);

  .mm-navbar {
    display: none;
  }
}

.mm-panel.mm-opened {
  transform: translate3d(0,0,0);
}

.mm-panel.mm-subopened {
  transform: translate3d(-30%,0,0);
}

.mm-vertical {
  .mm-panel {
    transform: none !important;
  }

  .mm-listview {
    .mm-panel {
      display: none;

      .mm-listview {
        > li {
          &:last-child {
            &:after {
              border-color: transparent;
            }
          }
        }
      }
    }

    > li {
      > .mm-next {
        height: 40px;
        bottom: auto;

        &:after {
          top: 16px;
          bottom: auto;
        }
      }
    }

    > li.mm-opened {
      > .mm-next {
        &:after {
          transform: rotate(45deg);
          right: 19px;
        }
      }
    }
  }

  li.mm-opened {
    > .mm-panel {
      display: block;
    }
  }
}

li.mm-vertical.mm-opened {
  > .mm-panel {
    display: block;
  }
}

.mm-clear {
  &:after {
    content: '';
    border: 2px solid transparent;
    display: block;
    width: 5px;
    height: 5px;
    margin: auto;
    position: absolute;
    top: 0;
    bottom: 0;
    transform: rotate(-45deg);
    border-left: none;
    border-top: none;
    right: 25px;
  }

  &:before {
    content: '';
    border: 2px solid transparent;
    display: block;
    width: 5px;
    height: 5px;
    margin: auto;
    position: absolute;
    top: 0;
    bottom: 0;
    transform: rotate(-45deg);
    border-right: none;
    border-bottom: none;
    right: 18px;
  }
}

.mm-close {
  &:after {
    content: '';
    border: 2px solid transparent;
    display: block;
    width: 5px;
    height: 5px;
    margin: auto;
    position: absolute;
    top: 0;
    bottom: 0;
    transform: rotate(-45deg);
    border-left: none;
    border-top: none;
    right: 25px;
  }

  &:before {
    content: '';
    border: 2px solid transparent;
    display: block;
    width: 5px;
    height: 5px;
    margin: auto;
    position: absolute;
    top: 0;
    bottom: 0;
    transform: rotate(-45deg);
    border-right: none;
    border-bottom: none;
    right: 18px;
  }
}

.mm-arrow {
  &:after {
    content: '';
    border: 2px solid transparent;
    display: block;
    width: 8px;
    height: 8px;
    margin: auto;
    position: absolute;
    top: 0;
    bottom: 0;
    transform: rotate(-45deg);
    border-top: none;
    border-left: none;
    right: 23px;
  }
}

.mm-next {
  &:after {
    content: '';
    border: 2px solid transparent;
    display: block;
    width: 8px;
    height: 8px;
    margin: auto;
    position: absolute;
    top: 0;
    bottom: 0;
    transform: rotate(-45deg);
    border-top: none;
    border-left: none;
    right: 23px;
  }
}

.mm-prev {
  &:before {
    content: '';
    border: 2px solid transparent;
    display: block;
    width: 8px;
    height: 8px;
    margin: auto;
    position: absolute;
    top: 0;
    bottom: 0;
    transform: rotate(-45deg);
    border-right: none;
    border-bottom: none;
    left: 23px;
  }
}

.mm-panel.mm-hasnavbar {
  .mm-navbar {
    display: block;
  }
}

.mm-menu.mm-vertical {
  .mm-listview:not(#navbar_menu) {
    li.mm-opened {
      .mm-panel {
        // Faculty colors for sublevel 2
        background: @gray;
        color: @gray-base;

        li:not(.last-child) {
          border-bottom: 1px solid darken(@gray, 5%);
        }

        .mm-next {
          &:after {
            // Arrows
            border-color: @gray-base;
          }
        }

        .mm-panel {
          // Faculty colors for sublevel 3
          background: lighten(@gray, 5%);

          li:not(.last-child) {
            border-bottom: 1px solid @gray;
          }

          .mm-panel {
            // Faculty colors for sublevel 4
            background: lighten(@gray, 10%);

            li:not(.last-child) {
              border-bottom: 1px solid @gray;
            }
          }
        }
      }

      > a.mm-next {
        .backgroundColorFix(@gray-base, .05);
      }
    }
  }
}

#navbar_menu {
  li.mm-opened {
    .mm-panel {
      background: lighten(@gray, 5%)!important;
    }
  }
}

.mm-menu.mm-offcanvas.mm-right {
  left: auto;
}

html.mm-right.mm-opening {
  .mm-slideout {
    transform: translate(-80%,0);
    transform: translate3d(-80%,0,0);
  }
}

html.mm-front {
  .mm-slideout {
    transform: none !important;
    z-index: 0;
  }

  #mm-blocker {
    z-index: 3;
  }

  .mm-menu.mm-offcanvas {
    z-index: 2000;
  }
}

.mm-menu.mm-offcanvas.mm-front {
  transition: transform 0.4s ease;
  transform: translate(-100%,0);
  transform: translate3d(-100%,0,0);
}

.mm-menu.mm-offcanvas.mm-next {
  transition: transform 0.4s ease;
  transform: translate(-100%,0);
  transform: translate3d(-100%,0,0);
}

.mm-menu.mm-offcanvas.mm-front.mm-right {
  transform: translate(100%,0);
  transform: translate3d(100%,0,0);
}

.mm-menu.mm-offcanvas.mm-next.mm-right {
  transform: translate(100%,0);
  transform: translate3d(100%,0,0);
}

.mm-menu.mm-offcanvas.mm-top {
  transform: translate(0,-100%);
  transform: translate3d(0,-100%,0);
  width: 100%;
  min-width: 100%;
  max-width: 100%;
  height: 80%;
  min-height: 140px;
  max-height: 880px;
}

.mm-menu.mm-offcanvas.mm-bottom {
  transform: translate(0,100%);
  transform: translate3d(0,100%,0);
  top: auto;
  width: 100%;
  min-width: 100%;
  max-width: 100%;
  height: 80%;
  min-height: 140px;
  max-height: 880px;
}

html.mm-opening {
  .mm-menu.mm-offcanvas.mm-front {
    transform: translate(0,0);
    transform: translate3d(0,0,0);
  }

  .mm-menu.mm-offcanvas.mm-next {
    transform: translate(0,0);
    transform: translate3d(0,0,0);
  }

  .mm-slideout {
    transform: translate(80%,0);
    transform: translate3d(80%,0,0);
  }
}

.mm-page {
  box-sizing: border-box;
  position: relative;
}

.mm-slideout {
  transition: transform 0.4s ease;
  z-index: 1;
}

html.mm-opened {
  overflow-x: hidden;
  position: relative;
}

html.mm-blocking {
  overflow: hidden;

  body {
    overflow: hidden;
  }

  #mm-blocker {
    display: block;
  }
}

.mm-background {
  .mm-page {
    background: inherit;
  }
}

#mm-blocker {
  display: none;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
}

.mm-menu.mm-offcanvas {
  z-index: 0;
  display: none;
  position: fixed;
  width: 80%;
  min-width: 140px;
  max-width: 440px;
}

.mm-menu.mm-offcanvas.mm-current {
  display: block;
}

.mm-menu.mm-offcanvas.mm-no-csstransforms.mm-current.mm-opened {
  z-index: 10;
}

html.mm-opened.mm-dragging {
  .mm-menu {
    -webkit-transition-duration: 0s;
    transition-duration: 0s;
  }

  .mm-slideout {
    -webkit-transition-duration: 0s;
    transition-duration: 0s;
  }
}

.mm-menu.mm-hoverselected {
  .mm-listview {
    > li {
      > a.mm-fullsubopen+a {
        -webkit-transition: background 0.4s ease;
        transition: background 0.4s ease;
      }

      > a.mm-fullsubopen+span {
        -webkit-transition: background 0.4s ease;
        transition: background 0.4s ease;
      }

      > a {
        &:not(.mm-fullsubopen) {
          -webkit-transition: background 0.4s ease;
          transition: background 0.4s ease;

          &:hover {
            .backgroundColorFix(@white-color, .5);
          }
        }
      }

      > a.mm-fullsubopen {
        &:hover+span {
          .backgroundColorFix(@white-color, .5);
        }
      }
    }
  }
}

.mm-menu.mm-parentselected {
  .mm-listview {
    > li {
      > a.mm-fullsubopen+a {
        -webkit-transition: background 0.4s ease;
        transition: background 0.4s ease;
      }

      > a.mm-fullsubopen+span {
        -webkit-transition: background 0.4s ease;
        transition: background 0.4s ease;
      }

      > a {
        &:not(.mm-fullsubopen) {
          -webkit-transition: background 0.4s ease;
          transition: background 0.4s ease;
        }
      }

      > a.mm-selected.mm-fullsubopen+a {
        .backgroundColorFix(@white-color, .5);
      }

      > a.mm-selected.mm-fullsubopen+span {
        .backgroundColorFix(@white-color, .5);
      }

      > a.mm-selected {
        &:not(.mm-fullsubopen) {
          .backgroundColorFix(@white-color, .5);
        }
      }
    }
  }
}

.mm-menu.mm-multiline {
  .mm-listview {
    > li {
      > a {
        text-overflow: clip;
        white-space: normal;
      }

      > span {
        text-overflow: clip;
        white-space: normal;
      }
    }
  }
}

.mm-sronly {
  text-indent: 10px;
  overflow: hidden;
  display: block;
  width: 1px;
  height: 1px;
  position: absolute;
  z-index: -1;
}

html.mm-pagedim-black {
  #mm-blocker {
    opacity: 0;
    background: @gray-base;
  }
}

html.mm-pagedim-black.mm-opening {
  #mm-blocker {
    opacity: 0.3;
    -webkit-transition: opacity 0.3s ease 0.3s;
    transition: opacity 0.3s ease 0.3s;
  }
}
@media all and (max-width:175px) {
  html.mm-right.mm-opening {
    .mm-slideout {
      transform: translate(-140px,0);
      transform: translate3d(-140px,0,0);
    }
  }

  html.mm-opening {
    .mm-slideout {
      transform: translate(140px,0);
      transform: translate3d(140px,0,0);
    }
  }
}
@media all and (min-width:550px) {
  html.mm-right.mm-opening {
    .mm-slideout {
      transform: translate(-440px,0);
      transform: translate3d(-440px,0,0);
    }
  }

  html.mm-opening {
    .mm-slideout {
      transform: translate(440px,0);
      transform: translate3d(440px,0,0);
    }
  }
}



