.gridbox .box1 {
  z-index: 4;
  order: 1;
  flex: 0 1 auto;
  position: relative;
}
.gridbox .box1 .header {
  color: inherit;
  font-size: 1.2em;
  text-transform: uppercase;
  padding: 8px 18px 6px 10px;
  font-weight: 700;
  letter-spacing: 0.4px;
  width: 100%;
}
.gridbox .box2 {
  padding: 5px 10px 10px;
  order: 2;
  flex: 1 1 auto;
}
.gridbox::before {
  display: inline-block;
  font-family: 'arrow';
  content: '\0043';
  color: #fff;
  bottom: 9px;
  right: 10px;
  position: absolute;
  z-index: 5;
}
.gridbox.no-arrow::before {
  display: none;
}
.gridbox.with-img .box2 {
  position: relative;
  padding: 0 !important;
  overflow: hidden;
  transform-style: preserve-3d;
}
.gridbox.with-img .box2 img {
  display: block;
  backface-visibility: hidden;
  max-width: 100%;
  height: auto;
  -ms-interpolation-mode: bicubic;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
}
.gridbox.with-img.img-left img {
  left: 0;
  transform: translateY(-50%) translateX(0);
}
.gridbox.with-img.img-right img {
  left: auto;
  right: 0;
  transform: translateY(-50%) translateX(0);
}
.gridbox:not(.with-img) .box1 {
  flex: 1 1 auto;
}
.gridbox:not(.with-img) .box2 {
  display: none;
}
.gridbox.with-iframe {
